export function initPageRules(params) {
    const div = document.createElement("div");
    div.className = "root";
    div.innerHTML = `
      <custom-text>Presioná jugar y elegí: piedra, papel o tijera antes de que pasen los 3 segundos.</custom-text>
      <div class="space">
      <blue-button class="start-button">Jugar</blue-button>
      </div>
      <div class="all-hands">
      <mano-comp option="tijera"></mano-comp>
      <mano-comp option="piedra"></mano-comp>
      <mano-comp option="papel"></mano-comp>
      </div>
      `;
    const startbuttonEL = div.querySelector(".start-button");
    startbuttonEL?.addEventListener("click", () => {
      params.goTo("/game");
    });
    return div;
  }  